<template>
  <div id="app">
    <img alt="Vue logo" src="./assets/logo.png">
    <h1>Aplikacja To-Do</h1>
    <h5>Swtórz swoją liste zadań</h5>
    <div class="AddTasks">
      <input maxlength="20"  type="text" v-model="todo" v-on:keyup.enter="createNewItem">
      <button class="add" @click="createNewItem()">+</button>
    </div>
    <ToDo v-for="todo in list" :todo="todo" @delete="onDeleteItem" :key="todo.id" />
    
  </div>
</template>

<script>
import ToDo from './components/ToDo.vue'
export default {
  name: 'app',
  components: {
    ToDo
  },
  data(){
    return{
      list: [
        {
          id: 0,
          text: 'Nauczyć się Vue!'
        },
      ],
      todo: "",
      newId:0,
    }
  },
  methods:{
    createNewItem(){
      this.newId++
      if(!this.todo){
        alert('Wpisz nowe zadanie!');
        return
      }
      
      this.list.push(
        {
          id: this.newId,
          text: this.todo,
        }
      );
      this.todo = "";
    },

    onDeleteItem(todo){
        this.list = this.list.filter(item => item !== todo);
    }
  }
}
</script>

