<template>
  <div class="ToDo-container">
    <div class="ToDo-item">
      <p class="ToDo-text" > {{todo.text}}</p>
      <button class="ToDo-delete" @click="deleteItem(todo)">x </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ToDo',
  props: ['todo'],
  methods:{
    deleteItem(todo){
      this.$emit('delete', todo)
    }
  }
}
</script>

